<template>
  <div class="container-fluid m-0 p-0">
    <div class="container style-of-institute">
      <h5 class="header bold mt-3">
        –ê—Ä—Ö–∏–≤ –¥–∞–Ω–Ω—ã—Ö –º–µ—Ç–µ–æ—Å—Ç–∞–Ω—Ü–∏–∏ –ò–í–ú –°–û –†–ê–ù –∑–∞ 3 –¥–Ω—è
      </h5>
    </div>
    <div class="container-fluid" v-for="item in dn" :key="item.name">
      <h5 class="text-center bold pt-5" v-if="item.type != 'winddirectcard'">
        {{ item.name }}
      </h5>
      <div
        class="container"
        v-bind:style="{ display: [media ? 'none' : 'inline'] }"
        v-if="item.type == 'winddirectcard'"
      >
        <CardWind :data="item" />
      </div>
      <div
        class="row justify-content-center"
        v-else-if="item.type == 'winddirect'"
      >
        <div
          class="col-sm-auto col-md-auto col-lg-auto col-xl-3 pt-4"
          v-for="(radarData, index) in item.values"
          :key="index"
        >
          <h6 class="text-center">{{ item.time[index].split(" ")[0] }}</h6>
          <RadarChart
            :data="{
              time: item.time,
              values: radarData,
              type: item.type,
              forecast: false,
            }"
          />
        </div>
      </div>
      <div
        v-bind:class="[media ? 'container-fluid p-0 m-0' : 'container']"
        v-else
      >
        <BarChart :data="item" />
      </div>
    </div>
  </div>
</template>
<script>
import BarChart from "./BarChart";
import RadarChart from "./RadarChart";
import CardWind from "./CardWind";
export default {
  props: ["dn"],
  data() {
    return {
      media: false,
    };
  },
  components: {
    BarChart,
    RadarChart,
    CardWind,
  },
  mounted() {
    const mediaQuery = window.matchMedia("(max-width: 1040px)");
    this.handleTabletChange(mediaQuery);
    mediaQuery.addListener(this.handleTabletChange);
  },

  methods: {
    handleTabletChange(e) {
      if (e.matches) {
        this.media = true;
      } else {
        this.media = false;
      }
    },
  },
};
</script>

<style>
.style-of-institute {
  color: #4472c4;
  padding: 0px;
  margin: 0px;
  border-top: 3px solid #4472c4;
}
.widthRadar {
  width: 50vh;
}
</style>